import { INDENT_INCREASE, INDENT_DECREASE } from '../actions';
import increaseIndent from '../commands/increaseIndent';
import decreaseIndent from '../commands/decreaseIndent';

export default function onAction(action, editor, next) {
  const { type } = action;
  if (type === INDENT_INCREASE) {
    return increaseIndent(editor);
  }
  if (type === INDENT_DECREASE) {
    return decreaseIndent(editor);
  }
  return next();
}
